.chat-box {
  &::-webkit-scrollbar {
    display: none;
  }

  .chat-date {
    position: relative;
    width: 100%;
    height: 1px;
    display: block;
    background: var(--chat-date);
    margin: 50px 0;

    &::after {
      content: attr(data-date);
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      width: 90px;
      height: 30px;
      background: var(--curier-sms-bg);
      border-radius: 57px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-weight: 500;
      font-size: 12px;
      line-height: 15px;
      letter-spacing: -0.5px;
      color: var(--ingrediend-text);
    }
  }

  .sms-box {
    padding: 10px 15px 30px 15px;

    .admin-message-wrapper {
      display: flex;
      justify-content: flex-end;
    }

    .admin-message {
      position: relative;
      background: var(--menubg);
      border-radius: 25px 25px 0 25px;
      .double-check {
        position: absolute;
        bottom: 0;
        right: 4px;
      }
      .time {
        right: 0;
      }
    }

    .user-message {
      background: var(--curier-sms-bg);
      border-radius: 25px 25px 25px 0;

      .time {
        left: 0;
      }
    }

    .admin-message,
    .user-message {
      position: relative;
      width: max-content;
      max-width: 256px;
      padding: 15px 20px;
      margin-top: 35px;
      font-weight: 500;
      font-size: 16px;
      line-height: 19px;
      letter-spacing: -0.5px;
      color: var(--primary-text);
      word-wrap: break-word;
    }

    .time {
      margin-top: 4px;
      position: absolute;
      top: 100%;
      font-weight: 500;
      font-size: 10px;
      line-height: 12px;
      letter-spacing: -0.5px;
      color: var(--ingrediend-text);
    }

    @media (max-width: 768px) {
      margin-bottom: 100px;
    }
  }

  .sms-input {
    position: fixed;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    bottom: 0;
    width: 400px;
    padding: 20px 35px;
    min-height: 77px;
    background: var(--user-sms-bg);
    box-shadow: 0 1px 2px rgba(169, 169, 150, 0.13);

    textarea {
      width: 85%;
      border: none;
      outline: none;
      background: transparent;
      color: var(--primary-text);

      &::placeholder {
        font-weight: 500;
        font-size: 12px;
        line-height: 15px;
        color: var(--ingrediend-text);
      }
    }

    .send-btn {
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      width: 37px;
      height: 37px;
      background: #45a524;
    }

    @media (max-width: 768px) {
      width: 100%;
    }
  }
}

.chat-container {
  border: 1px solid var(--border-color);
  * {
    font-family: 'Inter', sans-serif !important;
  }
  .chat-sidebar {
    border-color: var(--border-color) !important;
  }
  .chat-container {
    border-color: var(--border-color) !important;
    .chat-header {
      border-color: var(--border-color) !important;
      background-color: var(--grey);
      .cs-conversation-header__user-name {
        background-color: var(--grey);
      }
    }
    .chat-input {
      border-color: var(--border-color);
      .cs-button {
        color: var(--primary);
      }
      .cs-message-input__content-editor-wrapper {
        background-color: var(--menubg);
        div {
          background-color: var(--menubg);
        }
      }
    }
  }
}

.more-btn {
  color: var(--primary);
  cursor: pointer;
}

[data-theme='dark'] .chat-container {
  background-color: var(--dark-background);
  border-color: var(--dark-grey) !important;
  .cs-sidebar--left {
    background-color: var(--dark-background);
  }
  .cs-conversation-list,
  .cs-conversation,
  .cs-conversation__name {
    color: var(--light-text);
  }
  .cs-conversation__info {
    color: var(--dark-muted-text);
  }
  .cs-conversation:hover {
    background-color: var(--dark-primary-hover);
  }
  .cs-message-list {
    background-color: var(--dark-background);
    color: var(--light-text);
  }
  .chat-sidebar {
    border-color: var(--dark-grey) !important;
  }
  .chat-header {
    background-color: var(--dark-background);
    border-color: var(--dark-grey) !important;
    .cs-conversation-header__user-name {
      background-color: transparent;
      color: var(--light-text);
    }
  }
  .chat-input {
    border-color: var(--dark-grey) !important;
  }
  .cs-message-input {
    background-color: var(--dark-background);
    color: var(--light-text);
  }
  .cs-message-input__content-editor-wrapper {
    background-color: rgb(40, 49, 66) !important;
    border: 1px solid var(--dark-grey);
  }
  .cs-message-input__content-editor-wrapper div {
    background-color: rgb(40, 49, 66) !important;
  }
  .cs-message-input__content-editor {
    color: var(--dark-text-muted);
  }
  .cs-message-input__content-editor[data-placeholder]:empty:before {
    color: var(--light-text);
  }
  .chat-box .sms-box .admin-message,
  .chat-box .sms-box .user-message {
    background: var(--dark-primary-hover);
    color: var(--light-text);
    .time {
      color: var(--dark-muted-text);
    }
  }
  .chat-box .chat-date {
    background: var(--dark-grey);
    &::after {
      background: var(--dark-grey);
      color: var(--light-text);
    }
  }
}
